/* App*/
body {
  margin: 0;
  padding: 0;
  color: #555;
  line-height: 25px;
  font-weight: 400;
  background: #fff;
  font-family: Passion One, sans-serif; }

img {
  size: 50%; }

.App {
  text-align: left;
  margin-left: 3rem;
  margin-right: 3rem; }

.App-logo {
  size: 3rem; }

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
